
import React, { Component } from 'react'
import axios from 'axios'
import tab, { Tab, TabPanel, Tabs } from 'react-tabs'
import TabList from 'react-tabs/lib/components/TabList';
import { Link } from 'react-router-dom';

const url='https://developerfunnel.herokuapp.com/hotelsdetails/'

class HotelDetails extends Component{

    constructor(){
        super();

        this.state={
            HotelDetails:'',
            tripID:''
        }
    }

    

    render(){
        let {HotelDetails}=this.state
        return(
            <div>
               <div class="panel panel-primary">
                    <div class="panel-heading">
                        <div className="row">
                            <div className="col-sm-11" >
                                {HotelDetails.name}
                            </div>
                            <div className="col-sm-1" >
                            <Link to={`/List/${this.state.tripID}`} className='btn btn-danger'>Back</Link>
                            </div>
                        </div>
                    </div>
                    <div class="panel-body">
                        <div className="row">
                            <div className="col-sm-12" >
                            <img src={HotelDetails.thumb} />
                            </div>
                        </div>
                        <div className="row">
                            
                            <Tabs style={{marginLeft:'15px',marginTop:'35px'}}>
                                <TabList>
                                    <Tab>About</Tab>
                                    <Tab>Contact</Tab>
                                    <Tab>Aminities</Tab>
                                </TabList>
                       
                                
                                <TabPanel>
                                    <h2>About The place</h2>
                                    <h3>Cost: Rs.{HotelDetails.cost}</h3>
                                </TabPanel>
                                <TabPanel>
                                    <h2>Contact</h2>
                                    <h3>Phone Number: 78678678875</h3>
                                </TabPanel>
                                <TabPanel>
                                    <h2>Aminities</h2>
                                </TabPanel>

                            </Tabs>
                        </div>
                    </div>
                    &nbsp; &nbsp;

                    <Link to={`/placeBooking/${this.props.match.params.id}`} className='btn btn-success' style={{marginBottom:'15px'}}>Submit</Link>
                    
               </div>       
               

            </div>
        )
    }

    async componentDidMount(){
        const hotelId=this.props.match.params.id;
        const HotelType=sessionStorage.getItem('tripId')
        const response=await axios.get(`${url}${hotelId}`)
        console.log('hotelDetal',response)
        this.setState({HotelDetails:response.data[0]})
        this.setState({tripID:HotelType})
    }

    /*async componentDidMount(){
        let hotelid= this.props.match.params.id;
        let response = await fetch(`${url}/${hotelid}`, {method:'GET'});
        let data = await response.json()
        this.setState({hotel:data})
    }*/
    

}

export default HotelDetails;