import React,{Component} from 'react'
import './Search.css'
import { withRouter  } from "react-router-dom";

class Search extends Component{
   


    constructor(){
        super()

        this.state={
            location:'',
            cityHotel:''
        }
    }

    renderCity=()=>{
        console.log("renderCity Data",this.state.location)
        
        if(this.state.location){
            console.log("renderCity  inside Data",this.state.location)

            const citydata= this.state.location.map((data)=>{
                console.log("renderCity  inside loop Data",data)
                return(
                <option value={data._id}>{data.city_name}</option> 
                )
            })

            return citydata;
        }
    }

    getCityHotel=(event)=>{
        console.log(event.target.value);
        const cityId=event.target.value;
        fetch(`https://developerfunnel.herokuapp.com/hotels?city=${cityId}`,{method:'GET'})
        .then((response)=> response.json())
         .then(data=>{
             console.log("cityHotel",data)
             this.setState({cityHotel:data})
         })


    }


    renderCityHotel=(data)=>{
        console.log("renderCityHotel Data",this.state.cityHotel)
        
        if(data){
            console.log("renderCityHotel  inside Data",data)

            return data.map((item)=>{
                console.log("renderCityHotel  inside loop Data",item)
                return(
                <option value={item._id}>{item.name} | {item.city_name}</option> 
                )
            })

            
        }
    }

    placeBooking=(event)=>{
        console.log("this.props",this.props);
        this.props.history.push(`/hotel/${event.target.value}`)
        
    }

    render(){
        console.log("Render Data",this.state.location)
        return(
            <div>
                <div className='imageContainer'>
                    <div id="logo">
                    <b>D!</b>
                    </div>
                    <div className="heading">
                        Plan Trip With Us
                    </div>
                    <div className='locationSelector'>
                        <select onChange={this.getCityHotel}>
                            <option>------------City-------------</option>
                            {this.renderCity()}
                        </select>
                        <select  onChange={this.placeBooking}>
                            <option>------------Location-------------</option>
                            {this.renderCityHotel(this.state.cityHotel)}
                        </select>
                    </div>


                </div>
            </div>
        )
    }

    componentDidMount=()=>{

        fetch('https://developerfunnel.herokuapp.com/location',{method:'GET'})
        .then((response)=> response.json())
         .then(data=>{
             console.log("data",data)
             this.setState({location:data})
         })
    }
}

export default withRouter (Search);