import React, { Component } from 'react'
import axios from 'axios'
import ListingDisplay from './ListingDisplay'
import RoomFilter from '../Filter/RoomFilter'
import CostFilter from '../Filter/CostFilter'

const lurl='https://developerfunnel.herokuapp.com/hotellist/';

class ListingApi extends Component{

    constructor(props){
        super(props);

        this.state={
            Listing:''
        }
    }

    filteredHotel(data){
        this.setState({Listing:data})
    }

    filteredHotelByCost(data){
        this.setState({Listing:data})
    }

    render(){
        console.log('Listing data',this.state.Listing)
        return(
            <div>
                <div className="row">
                    <div className="col-md-2">
                        <RoomFilter filterhotel={(data)=>{this.filteredHotel(data)}}/>
                        <CostFilter roomperCost={(data)=>{this.filteredHotelByCost(data)}}/>

                    </div>
                    <div className="col-md-10">
                        <ListingDisplay listdata={this.state.Listing}/>
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount =()=>{

            sessionStorage.setItem('tripId',this.props.match.params.id);

            axios(`${lurl}${this.props.match.params.id}`,{Method:'GET'})
            .then((response)=>{this.setState({Listing:response.data})})

    }

}

export default ListingApi;