import React,{Component} from 'react'
import QuickSearchDisplay from './QuickSearchDisplay'


const burl='https://developerfunnel.herokuapp.com/booking';
class QuickSearch extends Component{

    constructor(){
        super()

        this.state={
            bookingType:''
        }

    }

    render(){
        return(
            <div>

               <QuickSearchDisplay bookingdata={this.state.bookingType}  />

            </div>
        )
    }

    componentDidMount=()=>{
        fetch(burl,{method:'GET'})
        .then((response)=> response.json())
         .then(data=>{
             console.log("cityHotel",data)
             this.setState({bookingType:data})
         })
    }
}

export default QuickSearch;